.wrapper {
    min-height: 100vh;
    width: 100%;
    background: #222222;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.start_new_game {
    width: 200px;
    height: 30px;
    margin-bottom: 25px;
    margin-top: 25px;
    cursor: pointer;
    color: white;
    outline: none;
    background: #222222;
    border: 1px solid white;
}

.start_new_game:hover {
    background: white;
    color: #222222;
    transition: .3s all ease;
}

.winner__info{
    margin-top: 25px;
    font-size: 36px;
    color: white;
}